import { css, cx } from "@emotion/css";
import React from "react";
import { Link } from "react-router-dom";
import { brandColors } from "../../theme/colors";
import { DeviceType } from "../../theme/device";
import { fontSize } from "../../theme/fontSize";
import { useDeviceType } from "../../theme/media";
import { maxPageSize, spaceRem } from "../../theme/spacing";

export const Header = () => {
  const deviceType = useDeviceType();
  const styles = makeStyles({ deviceType });
  return (
    <header className={styles.component}>
      {/* <h1 className={css({ textAlign: "center" })}> */}
      {/* TODO Align with artwork and image */}
      <div className={styles.content}>
        <strong>Sonicthames</strong>
        <nav>
          <Link to="/main">Home</Link>
          <Link to="/works">Sounds</Link>
          <Link to="/about">About</Link>
          <Link to="/contact">Contact</Link>
        </nav>
      </div>
    </header>
  );
};

const makeStyles = ({ deviceType }: { deviceType: DeviceType }) =>
  ({
    component: css({
      position: "sticky",
      top: 0,
      backgroundColor: brandColors.neve.primary,
      fontSize: fontSize("l"),
      gap: spaceRem(),
      textTransform: "uppercase",
      nav: css({
        display: "flex",
        gap: spaceRem(),
      }),
      zIndex: 1,
    }),
    content: cx(
      css({
        strong: css({
          flex: 1,
        }),
        display: "flex",
        justifyContent: "flex-end",
        gap: spaceRem(),
        paddingBottom: spaceRem(),
        paddingTop: spaceRem(),
      }),
      deviceType === "desktop"
        ? css({
            margin: "0 auto",
            maxWidth: maxPageSize,
          })
        : css({
            paddingLeft: spaceRem(),
            paddingRight: spaceRem(),
          })
    ),
  } as const);
